function logout(){
	alert('logout');
}

export default function Header(props) {
    const {user} = props;
    return (
        <nav id="topbar">
            <div className="topbar-header">
		    <p align="right">Welcome {user.name}</p>
                <button onClick={() => logout()}>Logout</button>
            </div>
        </nav>
    );
}